import {
    Popup,
    Form,
    Field,
    Button,
    Icon,
    Notify,
    Toast,
    Search,
    Swipe,
    SwipeItem ,
    Lazyload,
    Tab, 
    Tabs,
    Slider 
} from 'vant'

const vants = [Popup, Form, Field, Button, Icon, Notify, Toast, Search,Swipe,SwipeItem ,Lazyload,Tab, Tabs,Slider ]

export default {
    install(Vue) {
        vants.forEach(Componet => {
            Vue.use(Componet)
        })
    }
}